<template>
  <div class="tags">
    <span class="tag" v-for="semester in semesters" :class="getColor(semester.name)">{{ semester.name }}</span>
  </div>
</template>

<script>
export default {
  name: 'Tags',
  props: {
    semesters: Array
  },
  methods: {
    getColor(name) {
      const cleaned = name.trim()[0]

      if(cleaned === 'P') {
        return 'blue'
      }
      else if (cleaned === 'G') {
        return 'orange'
      }
      else {
        return 'green'
      }
    }
  }
}
</script>

<style scoped>
.tag {
  color: white;
}

.blue {
  background-color: #447ce8;
}

.green {
  background-color: #e85344;
}

.orange {
  background-color: #96b5af;
}
</style>