<template>
  <section class="section">
    <div class="container">
      <Header/>
      <Navigation @filter-monday="filterData" :lessons="data"/>
      <div class="block filter">
        <button @click="filterSemester(1)" class="button is-small">1. Semester</button>
        <button @click="filterSemester(2)" class="button is-small">2. Semester</button>
        <button @click="filterSemester(3)" class="button is-small">3. Semester</button>
        <button @click="filterSemester(4)" class="button is-small">4. Semester</button>
      </div>
      <div class="columns is-multiline">
        <div v-for="lesson in displayData" :key="lesson.title" class="column is-one-third">
          <div class="card">
            <div class="card-content">
              <h1 class="title is-size-5">{{ lesson.title }}</h1>
              <table class="table is-fullwidth">
                <tr>
                  <td>Dozent:</td>
                  <td>{{ lesson.lecturer }}</td>
                </tr>
                <tr>
                  <td>Veranstaltungsform:</td>
                  <td>{{ lesson.type }}</td>
                </tr>
                <tr>
                  <td>Ort:</td>
                  <td>{{ lesson.location }}</td>
                </tr>
                <tr>
                  <td>Modus:</td>
                  <td>{{ lesson.mode }}</td>
                </tr>
                <tr>
                  <th>Uhrzeit:</th>
                  <th>
                    <span class="icon-text">
                      <span class="icon">
                        <i class="fa-solid fa-clock"></i>
                      </span>
                      <span>{{ lesson.time['from'] }} - {{ lesson.time['to'] }}</span>
                    </span>
                  </th>
                </tr>
              </table>
              <div class="level">
                <div class="level-left">
                  <div class="tags">
                    <span class="tag">{{ lesson.lp }} LP</span>
                    <span class="tag">{{ lesson.sws }} SWS</span>
                  </div>
                </div>
                <div class="level-right">
                  <div class="tags">
                    <Tags :semesters="lesson.requirements"/>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import Header from './components/Header.vue'
import Navigation from './components/Navigation.vue'
import Tags from './components/Tags.vue'
import json from './assets/data.json'

export default {
  name: 'App',
  components: {
    Navigation,
    Tags,
    Header
  },
  props: {
    lessons: Array,
    semesters: Array
  },
  data() {
    return {
      data: json,
      displayData: json.filter((e) => {
        return e.day === 'montag'
      })
    }
  },
  methods: {
    filterSemester(semester) {
      this.displayData = this.displayData.filter((e) => {
        return e.requirements.some(x => x.name === 'Phil ' + semester) ||
        e.requirements.some(x => x.name === 'Ku ' + semester) ||
        e.requirements.some(x => x.name === 'Ges ' + semester)
      })
    },
    filterData(name) {
      if (name === 'all') {
        this.displayData = this.data
      } else {
        this.displayData = this.data.filter((e) => {
          return e.day === name
        })
      }
    }
  }
}
</script>

